let place = ['', '', '', '', '']
export default {
  'url': '/placeList/:type',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'place|5': [
        {
          'id|0-1000': Number('@id'),
          'name|+1': place 
        }
      ] 
    }
  }
}



