let place = ['', '', '', '', '']
export default {
  'url': '/controlArea/:type',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'controlArea|5': [
        {
          'id|0-1000': Number('@id'),          
          'name|+1': place,
          'value': '@csentence'
        }
      ] 
    }
  }
}



